* {
    padding: 0;
    margin: 0;
}

li {
    list-style: none;
}

a{
    text-decoration: none;
    color: black;
}

h2 {
    margin-top: 75px;
    text-align: center;
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
    font-size: 28px;
}


@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

@mixin size($width, $height) {
    width: $width;
    height: $height;
}


.wrap {
    @include size (1420px, 4300px);
    margin: 0 auto;
}

.fixed {
    position: fixed;
    top: 0;
    z-index: 9;
}
.header {
    width: 100%;
    height: 80px; 
    display: flex;
    line-height: 80px;
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
    background-color: white;
    border-bottom: 1px solid black;
    .header_container{
        @include size (1420px, 80px);
        margin: 0 auto;
        .logo {
            @include size (220px, 80px);
            text-align: center;
            float: left;
            img {
                height: 80px;
            }
        }
        .lnb {
            @include size (740px, 80px);
            display: flex;
            justify-content: space-around;
            float: left;
            position: relative;
            z-index: 1000;
            .shop_menu {
                ul{
                    position: absolute;
                    top: 81px;
                    left: -220px;
                    width: 445px;
                    height: 0px;
                    overflow: hidden;
                    transition: 0.3s ease-in;
                    li{
                        @include size (365px, 70px);
                        background-color: white;
                        border-bottom: 1px solid #a2a2a2;
                        border-right: 1px solid #a2a2a2;
                        border-left: 1px solid #a2a2a2;
                        padding-left: 70px;
                        font-size: 14px;
                        a{
                            display: block;
                        }
                        &:hover{
                            background-color: #efe8da;
                            cursor: pointer;
                        }
                    }
                }
            }
            .our_story_menu {
                ul{
                    position: absolute;
                    border-bottom: 1px solid #a2a2a2;
                    border-right: 1px solid #a2a2a2;
                    border-left: 1px solid #a2a2a2;
                    top: 81px;
                    left: -220px;
                    background-color: white;
                    box-sizing: border-box;
                    transition: 0.3s;
                    overflow: hidden;
                    width: 1420px;
                    height: 0;
                    &:hover{
                        background-color: #efe8da;
                    }
                    li{
                        float: left;
                        margin-right: 50px;
                        &:first-child{
                            margin-left: 375px;
                        }
                    }
                }
            }
        }   
        .gnb {
            width: 460px;
            display: flex;
            justify-content: flex-end;
            float: left;
            .search-container {
                position: relative;
                display: inline-block;
            }
    
            .search {
                @include size (160px, 25px);
                background-color: #FD8234;
                border-radius: 15px;
                margin-top: 30px;
                margin-right: 22px;
                border: none;
                outline: none;
                color: white;
                font-family: 'Poppins', sans-serif;
            }
    
            .search-button {
                position: absolute;
                top: 2px;
                right: 22px;
                height: 100%;
                background-color: transparent;
                border: none;
                outline: none;
                cursor: pointer;
                padding: 0 10px;
            }
    
            .fas.fa-search {
                color: white;
            }
    
            .search::-webkit-search-cancel-button {
                display: none;
            }
    
            .login {
                @include size (25px, 25px);
                margin-right: 20px;
            }
    
            .mypage {
                @include size (25px, 25px);
                margin-right: 20px;
            }
    
            .cart {
                @include size (25px, 25px);
                margin-right: 25px;
            }
        }
    }
}



@keyframes menuDown {
    0%{

    }
    100%{
        
    }
}
//--------------------------------------------------------------------------header


.main_visual {
    @include size (1420px, 820px);
    position: relative;
    overflow: hidden;

    img {
        position: absolute;
        width: 100%;
        height: 100%;
        transition: all 1s ease-out;
    }

    i {
        font-size: 60px;
        color: #fff;
        position: absolute;
        top: 410px;
        transform: translateY(-50%);

        &:hover {
            cursor: pointer;
        }
    }

    #mainNextButton {
        right: 0;
    }

    .dot {
        height: 14px;
        width: 14px;
        background-color: #fff;
        border-radius: 50%;
        display: inline-block;
        position: absolute;
        left: 50%;
        top: 90%;
        z-index: 5;
        transition: background-color 0.6s ease;

        &:hover {
            cursor: pointer;
            transform: scale(1.5);
        }
    }

    #dot0 {
        left: 46%;
    }

    #dot1 {
        left: 48%;
    }

    #dot3 {
        left: 52%;
    }

    #dot4 {
        left: 54%;
    }

    .dot.active {
        background-color: #111;
    }
}



//---------------------------------------------------------------------------main_visual

.new_arrival {
    @include size (1420px, 600px);
    position: relative;
    overflow: hidden;

    .new_arrival_slide {
        margin: 0 auto;
        margin-top: 50px;
        width: 1180px;
        height: 360px;
        position: relative;
        overflow: hidden;

        .new_arrival_contents {
            display: flex;
            width: 4720px;
            position: absolute;
            top: 0;
            left: 0;
            transition: transform 0.5s ease-in-out;

            .slide {
                div {
                    width: 360px;
                    height: 360px;
                    margin-right: 50px;
                    float: left;

                    img {
                        width: 100%;
                        user-select: none;
                    }

                    &:nth-child(3) {
                        margin-right: 0;
                    }
                }
            }
        }

        &:hover {
            cursor: pointer;
        }
    }

    i {
        font-size: 60px;
        color: #ccc;
        position: absolute;
        top: 250px;
    }

    .fa-chevron-left {
        top: 320px;
        left: 70px;
    }

    .fa-chevron-right {
        top: 320px;
        right: 70px;
    }

    .progress_bar {
        width: 100%;
        height: 12px;
        background-color: #ccc;
        position: absolute;
        bottom: 0;

        .progress {
            height: 100%;
            width: 25%;
            background-color: #FD8234;
            position: absolute;
            left: 0;
            transition: left 0.3s ease-in-out;
        }
    }
}

//---------------------------------------------------------------------------new_arrival

.best {
    @include size (1420px, 650px);
    margin-top: 50px;
    .swiper-container{
        margin: 20px auto;
        width: 1180px;
        height: 458px;
        overflow: hidden;
        position: relative;
        .swiper-wrapper{
            width: 700px;
            height: 400px;
            swiper-slide{
                &.swiper-slide-next{
                    width: 700px;
                    height: 700px;
                }
            }

        }
        img{
            width: 340px;
            height: 340px;
        }
        i{
            position: absolute;
            &::before{
                display: none;
            }
        }
        
    }
    }





//---------------------------------------------------------------------------best

.recommend {
    @include size (1420px, 750px);
    margin-top: 50px;

    ul {
        font-size: 18px;
        margin: 0 auto;
        margin-top: 30px;
        width: 575px;
        display: flex;
        font-weight: bold;
        justify-content: space-between;
        position: relative;

        .recommend_progress {
            width: 80px;
            height: 5px;
            background-color: #FD8234;
            position: absolute;
            margin-top: 10px;
            top: 25px;
            left: -18px;
        }
    }

    .recommend_container {
        height: 458px;
        width: 1180px;
        margin: 0 auto;
        margin-top: 30px;

        .recommend_main {
            width: 460px;
            height: 458px;
            margin-right: 20px;
            float: left;

            img {
                width: 100%;
                height: 100%;
            }
        }

        .recommend_side {
            width: 700px;
            height: 458px;
            float: left;

            div {
                width: 220px;
                height: 220px;
                float: left;
                margin-right: 20px;
                margin-bottom: 20px;

                &:nth-child(3) {
                    margin-right: 0;
                }

                &:nth-child(6) {
                    margin-right: 0;
                }

                img {
                    width: 100%;
                    height: 100%;
                }
            }
        }
    }

}


//---------------------------------------------------------------------------recommend

.brand_story {
    @include size (1420px, 700px);
    .brand_visual{
        width: 700px;
        height: 700px;
        float: left;
        margin-right: 20px;
        overflow: hidden;
        img{
            width: 100%;
            height: 100%;
            transition: 0.3s ease-in;
            &:hover{
                transform: scale(1.3);
            }
        }
    }
    .brand_text{
        width: 700px;
        height: 700px;
        float: left;
        h2{
            text-align: left;
            margin-left: 50px;
        }
        h3{
            margin-top: 125px;
            margin-left: 50px;
            font-size: 25px;
        }
        h4{
            margin-top: 47px;
            margin-left: 50px;
            font-weight: normal;
        }
        .detail_btn{
            margin-top: 50px;
            margin-left: 50px;
            width: 180px;
            height: 50px;
            background-color: #efe8da;
            font-weight: bold;
            line-height: 50px;
            text-align: center;
            cursor: pointer;
            &:hover{
                background-color: black;
                color: white;
            }
        }
    }
}

//---------------------------------------------------------------------------.brand_story

.insta{
    @include size(1420px, 700px);
    .insta_link{
        font-size: 16px;
        text-align: center;
        margin-top: 50px
    }
    .rolling{
        width: 1420px;
        height: 220px;
        overflow: hidden;
        margin-top: 100px;
        display: flex;
        .insta_picture{
            display: flex;
            li{
                width: 220px;
                height: 220px;
                margin-right: 20px;
                display: inline-block;
                img{
                    width: 100%;
                    height: 100%;
                }
            }
        }
    }
}

@keyframes flowRolling {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-100%);
    }
}

